function Header(){

  
    return(
        <main>
        <div className="relative px-6 lg:px-8">
        <img
        src="https://cgipv.udg.mx/sites/default/files/2022-09/220906_equipos_de_alumnos_de_la_udeg_lanzaran_cohetes_disenados_por_ellos_para_fines_cientificos_y_experimentales_aa8.jpg"
        alt=""
        className="absolute inset-0 -z-10 h-full w-full object-cover object-right md:object-center via-indigo-600 opacity-50"
      />
          <div className="mx-auto max-w-full xl:mx-12 xl:pt-40 xl:pb-64 lg:pt-40 lg:pb-48 pt-24 pb-12">
            <div>
              <div>
                <h1 className="text-4xl font-bold tracking-tight sm:text-left sm:text-6xl">
                Quienes somos?   
                </h1>
                <p className="mt-6 text-lg leading-8 text-gray-600 sm:left-center">
                  
                </p>
                <p className="mt-6 text-2xl leading-8 text-black-600 sm:text-left">
                "La exploracin del espacio ser nuestra respuesta final a la bsqueda de aventuras y de nuevos horizontes." - John Glenn.
                </p>
              </div>
              <div className="absolute inset-x-0 top-[calc(100%-13rem)] -z-10 transform-gpu overflow-hidden blur-3xl sm:top-[calc(100%-30rem)]">
                <svg
                  className="relative left-[calc(50%+3rem)] h-[21.1875rem] max-w-none -translate-x-1/2 sm:left-[calc(50%+36rem)] sm:h-[42.375rem]"
                  viewBox="0 0 1155 678"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fill="url(#ecb5b0c9-546c-4772-8c71-4d3f06d544bc)"
                    fillOpacity=".3"
                    d="M317.219 518.975L203.852 678 0 438.341l317.219 80.634 204.172-286.402c1.307 132.337 45.083 346.658 209.733 145.248C936.936 126.058 882.053-94.234 1031.02 41.331c119.18 108.451 130.68 295.337 121.53 375.223L855 299l21.173 362.054-558.954-142.079z"
                  />
                  <defs>
                    <linearGradient
                      id="ecb5b0c9-546c-4772-8c71-4d3f06d544bc"
                      x1="1155.49"
                      x2="-78.208"
                      y1=".177"
                      y2="474.645"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stopColor="#9089FC" />
                      <stop offset={1} stopColor="#FF80B5" />
                    </linearGradient>
                  </defs>
                </svg>
              </div>
            </div>
          </div>
        </div>
      </main>
    )



}

export default Header


