

import { connect } from "react-redux"
import {NavLink, Link} from 'react-router-dom'
import logo_enmice from 'assets/img/Logo.webp'
import {useState, userState} from 'react'
import loading from 'assets/img/loading.gif'
import  DotLoader from "react-spinners/DotLoader"

function Navbar(){


    const [loading, setLoading] = useState(true)  

    window.onscroll = function() {scrollFunction()}

    function scrollFunction(){
      if(document.getElementById('navbar')){
        if (document.body.scrollTop > 50 || document.documentElement.scrollTop > 50){
          document.getElementById('navbar').classList.add('shadow-navbar');
          document.getElementById('navbar').classList.add('bg-white');
        }else{
            document.getElementById('navbar').classList.remove('shadow-navbar');
            document.getElementById('navbar').classList.remove('bg-white');
        }
      }
    }



    return(
        <nav id="navbar" className="w-full py-5 top-0 transition duration-300 ease-in-out bg-white z-40 fixed">
            <div className="px-4 sm:px-6">
      <div className="-ml-4 -mt-2 flex flex-wrap items-center justify-between sm:flex-nowrap md:px-12 px-2">
        <Link to = '/Home' className="ml-4 mt-2">
          <img 
          src={logo_enmice}
          width={100}
          height={100}
          className=""/>
          
          <h3 className="text-lg font-medium leading-6 text-gray-900">ENMICE - 2023</h3>
        </Link>
        <div className="ml-4 mt-2 flex-shrink-0">
        <NavLink to= '/Convocatoria'className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">Convocatoria</NavLink>
        <NavLink to= '/Registro' className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">Registro</NavLink>
        <NavLink to= '/ENMICE' className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">ENMICE</NavLink>
        <NavLink to= '/Quienes' className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">Quienes somos?</NavLink>
        <NavLink to= '/Educacion' className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">Educacion</NavLink>
        <NavLink to= '/Blog' className="text-lg inline-flex font-medium leading-6 text-gray-900 hover:text-orange-500  mx-5">Blog</NavLink>
          <button
            type="button"
            to = '/Home'
            className="ml-12 relative inline-flex items-center rounded-md border border-transparent bg-orange-button px-3 py-2 text-sm font-bold text-white shadow-sm hover:bg-black focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
          >
            Inicio
            
            <DotLoader loading={loading} size = {15} color = '#c63782'/>
          </button>
        </div>
      </div>
    </div>
        </nav>  
    )
}

const mapStateToProps = state =>({

})

export default connect(mapStateToProps, {

}) (Navbar) 

