const incentives = [
    {
      name: 'Comit Ejecutivo Organizador y de Logstica (COL)',
      imageSrc: 'https://tailwindui.com/img/ecommerce/icons/icon-shipping-simple.svg',
      description: "Encargado de las tareas organizativas y medios necesarios para llevar a cabo la realizacin de las actividades que integran el evento. Este comit est integrado por miembros del Comit Ejecutivo y voluntarios.",
    },
    {
      name: 'Comit de Ingeniera y Reglamentacin (CIR)',
      imageSrc: 'https://tailwindui.com/img/ecommerce/icons/icon-warranty-simple.svg',
      description: "encargado de la definicin, revisin y aplicacin para los criterios de evaluacin de los aspectos tcnicos y de ingeniera que se involucren en las actividades del ENMICE. En esta edicin, el CIR est conformado por acadmicos de la Universidad Marista de Guadalajara y miembros del Comit Ejecutivo del evento.",
    },
  ]
  
  export default function Incentives() {
    return (
      <div className="bg-white">
        <div className="mx-auto max-w-7xl py-24 sm:px-2 sm:py-32 lg:px-4">
          <div className="mx-auto max-w-2xl px-4 lg:max-w-none">
            <div className="max-w-3xl">
              <h2 className="text-4xl font-bold tracking-tight text-gray-900">
                Organizacion
              </h2>
              <p className="mt-4 text-gray-500">
              El Encuentro Mexicano de Ingeniera en Cohetera Experimental (ENMICE) se conforma de la
siguiente manera:

              </p>
            </div>
            <div className="mt-16 grid grid-cols-1 gap-y-10 gap-x-8 lg:grid-cols-3">
              {incentives.map((incentive) => (
                <div key={incentive.name} className="sm:flex lg:block">
                  <div className="sm:flex-shrink-0">
                    <img className="h-16 w-16" src={incentive.imageSrc} alt="" />
                  </div>
                  <div className="mt-4 sm:mt-0 sm:ml-6 lg:mt-6 lg:ml-0">
                    <h3 className="text-sm font-medium text-gray-900">{incentive.name}</h3>
                    <p className="mt-2 text-sm text-gray-500">{incentive.description}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    )
  }